import { useEffect, useState } from "react";
import style from "../PayrollManager.module.css";
import {
  usePayrollDashboardMaster,
  usePayrollUpdateEmployer,
} from "../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import FormInput from "../../../Ui/Input/FormInput";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useLocation } from "react-router-dom";

const PayrollEmployerDetail = ({ pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [state, setState] = useState({
    employer_name: "",
    employer_address: "",
    employer_pincode: "",
    employer_state: "",
    employer_email: "",
    employer_contact: "",
    employer_district: "",
    employer_pan: "",
    employer_tan: "",
    employer_cin: "",
    employar_gstin: "",
    pay_slip_name: "",
    pay_slip_authority: "",
    pay_slip_signature: "",
  });
  const [disabled, setDisabled] = useState(false);
  //   const [errorField, setErrorField] = useState("");

  const [payrollUpdateEmployer] = usePayrollUpdateEmployer();

  const { payrollDashboardMaster } = usePayrollDashboardMaster({
    data: {
      pid: getQuery.state?.pid,
      mid: "",
    },
    skip: !getQuery.state?.pid,
  });

  useEffect(() => {
    if (payrollDashboardMaster?.employer_details) {
      let st = payrollDashboardMaster?.employer_details;
      setState({
        employer_name: st?.employer_name ?? "",
        employer_address: st?.employer_address ?? "",
        employer_pincode: st?.employer_pincode ?? "",
        employer_state: st?.employer_state ?? "",
        employer_email: st?.employer_email ?? "",
        employer_contact: st?.employer_contact ?? "",
        employer_district: st?.employer_district ?? "",
        employer_pan: st?.employer_pan ?? "",
        employer_tan: st?.employer_tan ?? "",
        employer_cin: st?.employer_cin ?? "",
        employar_gstin: st?.employar_gstin ?? "",
        pay_slip_name: st?.pay_slip_name ?? "",
        pay_slip_authority: st?.pay_slip_authority ?? "",
        pay_slip_signature: st?.pay_slip_signature ?? "",
      });
    }
  }, [payrollDashboardMaster?.employer_details]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onUpdate = () => {
    if (pid) {
      setDisabled((pre) => !pre);
      payrollUpdateEmployer({
        pid: pid,
        bodyParameter: {
          employer_details: state,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <div className={style.ped_container}>
        <FormInput
          label={t("employer_name")}
          placeholder={t("employer_name_placeholder")}
          name="employer_name"
          type="text"
          value={state.employer_name}
          onChange={onChange}
          customFormInputContainer={{
            width: "100%",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employer_pan")}
          placeholder={t("employer_pan_placeholder")}
          name="employer_pan"
          type="text"
          value={state.employer_pan}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employer_tan")}
          placeholder={t("employer_tan_placeholder")}
          name="employer_tan"
          type="text"
          value={state.employer_tan}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employer_email")}
          placeholder={t("employer_email_placeholder")}
          name="employer_email"
          type="text"
          value={state.employer_email}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employer_contact")}
          placeholder={t("employer_contact_placeholder")}
          name="employer_contact"
          type="text"
          value={state.employer_contact}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormTextarea
          label={t("employer_address")}
          placeholder={t("employer_address_placeholder")}
          name="employer_address"
          type="text"
          value={state.employer_address}
          onChange={onChange}
          customFormInputContainer={{
            width: "100%",
          }}
        />

        <FormInput
          label={t("employer_state")}
          placeholder={t("employer_state_placeholder")}
          name="employer_state"
          type="text"
          value={state.employer_state}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />

        <FormInput
          label={t("employer_district")}
          placeholder={t("employer_district_placeholder")}
          name="employer_district"
          type="text"
          value={state.employer_district}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employer_pincode")}
          placeholder={t("employer_pincode_placeholder")}
          name="employer_pincode"
          type="text"
          value={state.employer_pincode}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employer_cin")}
          placeholder={t("employer_cin_placeholder")}
          name="employer_cin"
          type="text"
          value={state.employer_cin}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("employar_gstin")}
          placeholder={t("employer_cin_placeholder")}
          name="employar_gstin"
          type="text"
          value={state.employar_gstin}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("pay_slip_name")}
          placeholder={t("pay_slip_name_placeholder")}
          name="pay_slip_name"
          type="text"
          value={state.pay_slip_name}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <FormInput
          label={t("pay_slip_authority")}
          placeholder={t("pay_slip_authority_placeholder")}
          name="pay_slip_authority"
          type="text"
          value={state.pay_slip_authority}
          onChange={onChange}
          customFormInputContainer={{
            width: "49%",
            marginBottom: "0.9rem",
          }}
          // errorShow={errorField["form_checklist_lable"]}
        />
        <CreateButton
          label="update"
          onAction={onUpdate}
          customStyle={{
            width: "100%",
          }}
        />
      </div>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default PayrollEmployerDetail;
